import React from "react";
import { getEnv, getSite } from "api/netlify";
import store from "store";
import { getFirebaseAppConfig } from "api/firebaseApp";
import { getAppearance, isSuperAdminSet, testAuthentication, testFirestore } from "api/adminUsers";
import { ConnectionState } from "types";
import { useSelector } from "react-redux";
import { RootState } from "store";

export const setColors = (colors: string[]) => {
  document.documentElement.style.setProperty("--secondary-lighter", colors[0]);
  document.documentElement.style.setProperty("--secondary-normal", colors[1]);
  document.documentElement.style.setProperty("--primary-normal", colors[2]);
  document.documentElement.style.setProperty("--primary-darker", colors[3]);
};

const useConfiguration = () => {
  const [checkCompleted, setCheckCompleted] = React.useState(false);
  const [adminSdkState, setAdminSdkState] = React.useState<ConnectionState>("not connected");
  const [firestoreWorks, setFirestoreWorks] = React.useState(false);
  const [authWorks, setAuthWorks] = React.useState(false);
  const [isAdminSet, setIsAdminSet] = React.useState(false);
  const [freezeApiCalls, setFreezeApiCalls] = React.useState(false);
  const [freezeCompleted, setFreezeCompleted] = React.useState(false);

  const { siteId, netlifyAccessToken } = useSelector((state: RootState) => ({
    siteId: state.siteId,
    netlifyAccessToken: state.netlifyAccessToken,
  }));
  const intervalRef = React.useRef<any>(null);
  const testIntervalRef = React.useRef<any>(null);
  const freezeIntervalRef = React.useRef<any>(null);

  React.useEffect(() => {
    (async () => {
      // netlify access token
      const netlifyAccessToken = localStorage.getItem("netlify-access-token-for-fireck");
      if (netlifyAccessToken != null) {
        store.dispatch({ type: "SET_NETLIFY_ACCESS_TOKEN", payload: netlifyAccessToken });
      }
      // netlify access token

      // project id | site id
      const envRes = await getEnv();
      if (envRes.SITE_ID) {
        store.dispatch({ type: "SET_SITE_ID", payload: envRes.SITE_ID });
      }
      if (envRes.firebase_project_id) {
        store.dispatch({ type: "SET_PROJECT_ID", payload: envRes.firebase_project_id });
        setAdminSdkState("connected");
      }
      if (envRes.apiFreezeTime && envRes.apiFreezeTime < Date.now() - 120000) {
        setFreezeCompleted(true);
      }

      // project id | site id
    })();
  }, []);

  React.useEffect(() => {
    (async () => {
      if (adminSdkState === "connected") {
        setCheckCompleted(false);
        const promises = [];

        // firestore crashes or not

        promises.push(
          testFirestore().then((res) => {
            if (res.error) {
              return;
            }
            setFirestoreWorks(true);
          })
        );

        // firestore crashes or not

        // auth crashes or not

        promises.push(
          testAuthentication().then((res) => {
            if (res.error) {
              return;
            }
            setAuthWorks(true);
          })
        );

        // auth crashes or not

        //firebase app config
        promises.push(
          getFirebaseAppConfig().then((res) => {
            if (!("error" in res)) {
              store.dispatch({ type: "SET_FIREBASE_APP_API_KEY", payload: res.apiKey });
            }
          })
        );

        //firebase app config

        // is admin set
        promises.push(
          isSuperAdminSet().then((res) => {
            if (!res.error) {
              setIsAdminSet(res);
            }
          })
        );

        await Promise.all(promises);
        // await new Promise((resolve, reject) => setTimeout(resolve, 200));
        setCheckCompleted(true);

        // is admin set
      }
    })();
  }, [adminSdkState]);

  React.useEffect(() => {
    const fn = async () => {
      const currentEnv = await getEnv();
      const site = await getSite(siteId, netlifyAccessToken);
      setAdminSdkState(
        currentEnv.firebase_project_id &&
          currentEnv.firebase_project_id === site.build_settings.env.firebase_project_id
          ? "connected"
          : site.build_settings.env.firebase_project_id
          ? "building"
          : "not connected"
      );
    };
    if (netlifyAccessToken && siteId && adminSdkState !== "connected" && !freezeApiCalls) {
      fn();
      intervalRef.current = setInterval(fn, 5000);
    }

    return () => clearInterval(intervalRef.current);
  }, [siteId, netlifyAccessToken, adminSdkState, freezeApiCalls]);

  React.useEffect(() => {
    const fn = async () => {
      const currentEnv = await getEnv();

      if (currentEnv.apiFreezeTime && currentEnv.apiFreezeTime < Date.now() - 120000) {
        setFreezeApiCalls(false);
        setFreezeCompleted(true);
      }
    };
    if (netlifyAccessToken && siteId && !freezeCompleted) {
      fn();
      freezeIntervalRef.current = setInterval(fn, 5000);
    }

    return () => clearInterval(freezeIntervalRef.current);
  }, [netlifyAccessToken, siteId, freezeCompleted]);

  React.useEffect(() => {
    const fn = async () => {
      if (!firestoreWorks) {
        const firestoreResponse = await testFirestore();
        if (!firestoreResponse.error) {
          setFirestoreWorks(true);
        }
      }

      if (!authWorks) {
        const authResponse = await testAuthentication();
        if (!authResponse.error) {
          setAuthWorks(true);
        }
      }
    };
    if (adminSdkState === "connected" && (!firestoreWorks || !authWorks) && !freezeApiCalls) {
      fn();
      testIntervalRef.current = setInterval(fn, 5000);
    }
    return () => clearInterval(testIntervalRef.current);
  }, [adminSdkState, firestoreWorks, authWorks, freezeApiCalls]);

  React.useEffect(() => {
    (async () => {
      const res = await getAppearance();
      if (!res.error) {
        store.dispatch({ type: "SET_APPEARANCE", payload: res });
        store.dispatch({ type: "SET_NEW_APPEARANCE", payload: res });
        setColors(res.colors);
      }
    })();
  }, [isAdminSet]);

  return {
    checkCompleted,
    adminSdkState,
    firestoreWorks,
    authWorks,
    isAdminSet,
    setIsAdminSet,
    setFreezeApiCalls,
    freezeApiCalls,
    freezeCompleted,
    setFreezeCompleted,
  };
};

export default useConfiguration;
